import now from "../../../getTime.js";
export default {
	data() {
		return {
			menuArrow: true,
			menuBorder: false,
			menuCard: false,
			date: '',
			index: 0,
			picker: ['按工点查询','按公司查询'],
			isCompany: false,
			foo: [],
			companyFoo: [],
			today: ''
		}
	},
	methods: {
		showLevel2(e) {
			
			var infoo = e.currentTarget.dataset.info;
			for (let s of this.companyFoo) {
				if (s.company == infoo.company) {
					s.shownext = !s.shownext;
				}
			}
			
		},
		showLevel(e) {
			
			var infoo = e.currentTarget.dataset.info;
			for (let s of this.foo) {
				if (s.XZCD == infoo.XZCD) {
					s.shownext = !s.shownext;
				}
			}
			
		},
		PickerChange(e) {
			this.index = e.detail.value;
			
			if (this.index == 0) {
				this.isCompany = false
			} else {
				this.isCompany = true;
			}
			this.getData();
		},
		DateChange(e) {
			this.date = e.detail.value;
			this.getData();
		},
		search() {
			this.getData()
		},
		showToast(title) {
			uni.showModal({
				title: '提示',
				content: title,
				showCancel: false,
				success: function(res) {
					if (res.confirm) {}
				}
			});
		},
		getData() {
			uni.showLoading({
				title:'加载中。。。',
				mask:true
			})
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Web/Query_CheckIn',
				data:{
					time: self.date,
					isCompany: self.isCompany
				}
			}).then(data => {
				var [error, res] = data;
				
				if (res.data.code == 0) {
					if (self.isCompany) {
						for (let s of res.data.data) {
							
							s.shownext = false;
							
						}
						self.companyFoo = res.data.data;
					} else {
						for (let s of res.data.data) {
							s.people = 0;
							s.shownext = false;
							if (s.companys.length>0) {
								for (let k of s.companys) {
									s.people = s.people + k.users.length;
								}
							} else {
								s.people =  0;
							}
						}
						self.foo = res.data.data;
					}
					
				} else {
					self.showToast('查询出错')
				}
				uni.hideLoading()
				
			})
		}
	},
	mounted() {
		this.date = now.now();
		this.today = now.now()
		this.getData()
	}
}
